import './nav.css'
import React, { useState, useEffect, useRef } from 'react';

const Nav = () => {
    const [navLink, setNavLink] = useState('#')
    const navRef = useRef(null);
    const prevScrollPos = useRef(window.pageYOffset);

  
    useEffect(() => {
      function handleScroll() {
        const currentScrollPos = window.pageYOffset;
        const navbar = navRef.current;
  
        if (prevScrollPos.current > currentScrollPos) {
          navbar.style.top = '0';
        } else {
          navbar.style.top = '-115px';
        }

        if (currentScrollPos === 0) {
            navbar.style.boxShadow = 'none';
          } else {
            navbar.style.boxShadow = '0px 2px 4px rgba(0, 0, 0, 0.5)';
          }
        prevScrollPos.current = currentScrollPos;
      }
  
      window.addEventListener('scroll', handleScroll);
  
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, []);

  return (
    <nav ref={navRef}>
      {
        <div className='navigation' id='navi'>
        <div className="logo">
          <div className="logo_container">
            <a href="">
              <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 245.145 172.19">
                  <g id="Group_1" data-name="Group 1" transform="translate(-138 -150)">
                    <g id="Path_2" data-name="Path 2" transform="translate(138 150)" fill="#0a192f">
                      <path d="M 201.5618286132812 130.1902923583984 L 8.583390235900879 130.1902923583984 C 7.533514976501465 130.1902923583984 6.533202648162842 129.8357696533203 5.838921546936035 129.2176055908203 C 5.456233978271484 128.8768615722656 5.000015258789062 128.3105773925781 5.000015258789062 127.5479507446289 L 5.000015258789062 7.642325878143311 C 5.000015258789062 6.879732131958008 5.456202507019043 6.313450813293457 5.838921546936035 5.972701072692871 C 6.533202648162842 5.354544639587402 7.533514976501465 5.00001335144043 8.583390235900879 5.00001335144043 L 201.5618286132812 5.00001335144043 C 202.6117095947266 5.00001335144043 203.6120147705078 5.354544639587402 204.3063201904297 5.972732067108154 C 204.6890106201172 6.313482284545898 205.1452331542969 6.879732131958008 205.1452331542969 7.642325878143311 L 205.1452331542969 100.0724487304688 L 205.1452331542969 127.5479507446289 C 205.1452331542969 128.3105773925781 204.6890106201172 128.8768310546875 204.3063201904297 129.2175750732422 C 203.6120147705078 129.8357696533203 202.6117095947266 130.1902923583984 201.5618286132812 130.1902923583984 Z" stroke="none"/>
                      <path d="M 10 9.999992370605469 L 10 125.1902847290039 L 200.1452178955078 125.1902847290039 L 200.1452178955078 9.999992370605469 L 10 9.999992370605469 M 8.583389282226562 0 L 201.5618286132812 0 C 206.3022918701172 0 210.1452178955078 3.42156982421875 210.1452178955078 7.642318725585938 L 210.1452178955078 127.5479278564453 C 210.1452178955078 131.7686767578125 206.3022918701172 135.1902770996094 201.5618286132812 135.1902770996094 L 8.583389282226562 135.1902770996094 C 3.8428955078125 135.1902770996094 0 131.7686767578125 0 127.5479278564453 L 0 7.642318725585938 C 0 3.42156982421875 3.8428955078125 0 8.583389282226562 0 Z" stroke="none" fill="#64ffda"/>
                    </g>
                    <g id="Path_3" data-name="Path 3" transform="translate(173 187)" fill="#0a192f">
                      <path d="M 201.5618286132812 130.1902923583984 L 8.583390235900879 130.1902923583984 C 7.533514976501465 130.1902923583984 6.533202648162842 129.8357696533203 5.838921546936035 129.2176055908203 C 5.456233978271484 128.8768615722656 5.000015258789062 128.3105773925781 5.000015258789062 127.5479507446289 L 5.000015258789062 7.642325878143311 C 5.000015258789062 6.879732131958008 5.456202507019043 6.313450813293457 5.838921546936035 5.972701072692871 C 6.533202648162842 5.354544639587402 7.533514976501465 5.00001335144043 8.583390235900879 5.00001335144043 L 201.5618286132812 5.00001335144043 C 202.6117095947266 5.00001335144043 203.6120147705078 5.354544639587402 204.3063201904297 5.972732067108154 C 204.6890106201172 6.313482284545898 205.1452331542969 6.879732131958008 205.1452331542969 7.642325878143311 L 205.1452331542969 100.0724487304688 L 205.1452331542969 127.5479507446289 C 205.1452331542969 128.3105773925781 204.6890106201172 128.8768310546875 204.3063201904297 129.2175750732422 C 203.6120147705078 129.8357696533203 202.6117095947266 130.1902923583984 201.5618286132812 130.1902923583984 Z" stroke="none"/>
                      <path d="M 10 9.999992370605469 L 10 125.1902847290039 L 200.1452178955078 125.1902847290039 L 200.1452178955078 9.999992370605469 L 10 9.999992370605469 M 8.583389282226562 0 L 201.5618286132812 0 C 206.3022918701172 0 210.1452178955078 3.42156982421875 210.1452178955078 7.642318725585938 L 210.1452178955078 127.5479278564453 C 210.1452178955078 131.7686767578125 206.3022918701172 135.1902770996094 201.5618286132812 135.1902770996094 L 8.583389282226562 135.1902770996094 C 3.8428955078125 135.1902770996094 0 131.7686767578125 0 127.5479278564453 L 0 7.642318725585938 C 0 3.42156982421875 3.8428955078125 0 8.583389282226562 0 Z" stroke="none" fill="#64ffda"/>
                    </g>
                  </g>
                  <text id="FIN" transform="translate(54 142)" fill="#64ffda" stroke="#64ffda" stroke-width="1" font-size="100" font-family="Verdana"><tspan x="0" y="0">FIN</tspan></text>
              </svg>
            </a>
          </div>
        </div>
        <div className="navName">
          <ul>
            <li data-aos="fade-down"
                data-aos-easing="linear"
                data-aos-delay="2100"
                data-aos-duration="300">
              <a href="#about" onClick={() => setNavLink('#about')} className={navLink === '#about' ? 'active' : ''}>
                <span>01. </span>About
              </a>
            </li>
            <li data-aos="fade-down"
                data-aos-easing="linear"
                data-aos-delay="2100"
                data-aos-duration="600">
              <a href="#experience" onClick={() => setNavLink('#experience')} className={navLink === '#experience' ? 'active' : ''}>
                <span>02. </span>Experience
              </a>
            </li>
            <li data-aos="fade-down"
                data-aos-easing="linear"
                data-aos-delay="2100"
                data-aos-duration="900">
              <a href="#projects" onClick={() => setNavLink('#projects')} className={navLink === '#projects' ? 'active' : ''}>
                <span>03. </span>Projects
              </a>
            </li>
            <li data-aos="fade-down"
                data-aos-easing="linear"
                data-aos-delay="2100"
                data-aos-duration="1200">
              <a href="#contact" onClick={() => setNavLink('#contact')} className={navLink === '#contact' ? 'active' : ''}>
                <span>04. </span>Contact
              </a>
            </li>
            <li data-aos="fade-down"
                data-aos-easing="linear"
                data-aos-delay="2100"
                data-aos-duration="1500">
              <a href="" className="btn resume">
                Resume
              </a>
            </li>
          </ul>
        </div>
      </div>
      }
    </nav>
    
  );
};

export default Nav;
